import React from "react"
// import { Link } from "gatsby"
import Grid from "@material-ui/core/Grid"

import Layout from "../components/layout"
import SEO from "../components/seo"
import PhotoProfile from "../components/photoProfile"
import Project from "../components/project"

import projects from "../projects.json"

const IndexPage = () => {
  return (
    <Layout>
      <SEO title="Home" />
      <div
        style={{
          flex: 1,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
          fontSize: 14,
        }}
      >
        <div style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}>
          <PhotoProfile />
        </div>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            flex: 1,
            textAlign: "center",
          }}
        >
          <p>
            Hi, People <br></br>
            I'm <strong> Abraham Kamell</strong> from Colombia.
          </p>
          <p style={{ textAlign: "justify" }}>
            I'm Software Engineer. In 2010 I started my studies in software
            engineering. <br></br> I work at{" "}
            <a href="https://www.lean-tech.io/"> Lean Tech IO </a> in Medellin,
            CO. <br></br> I have knowledge in frontend and backend technologies
            but I have more experience as a backend developer. Let me just tell
            you a few projects about my own experience:
          </p>

          <Grid container spacing={3}>
            {projects.map(p => {
              return (
                <Project
                  key={p.key}
                  year={p.year}
                  company={p.company}
                  chipData={p.technologies}
                >
                  {p.description}
                </Project>
              )
            })}
          </Grid>

          <p style={{ textAlign: "justify" }}>
            {
              // I want to write to help people like me, here you will find a list of
              // posts that could help you.
              // <Link to="/page-2/">Go to page 2</Link>
            }
          </p>
        </div>
      </div>
    </Layout>
  )
}

export default IndexPage
