import React from "react"
import { Chip } from "@material-ui/core"
import Grid from "@material-ui/core/Grid"
import "./project.css"

const Project = props => {
  return (
    <Grid item xs={12} sm={6}>
      <div
        style={{
          flexDirection: "column",
          textAlign: "justify",
          marginBottom: 20,
        }}
        className="container"
      >
        <strong>
          {props.year}, {props.company}
        </strong>
        <br></br>
        {props.children}
        <br></br>
        {props.chipData.map(data => {
          return (
            <Chip
              key={data.key}
              label={data.label}
              size="small"
              style={{ marginTop: 5, marginRight: 5, fontSize: 12 }}
            />
          )
        })}
      </div>
    </Grid>
  )
}

export default Project
